#root{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  font-size: 4em;
  width: 1.25em;
}
.button-num{
  width: 1.25em;
  flex-grow: 1;
}
.rows{
  display: flex;
  flex-flow: row;
  flex-grow: 1;
}
#zero{
  flex-grow: 2;
}
#calcbox{
  display: flex;
  flex-direction: column;
  align-items: space-around;
  background-color: black;
  border: 0.5em solid black;
  border-radius: 2em 2em 0 0;
  max-width: 45em;
  
}
.disp{
  flex-grow: 1;
  text-align: right;
  background-color: grey;
  padding: 0.2em 0.2em 0 0.2em; 
  font-family: 'Orbitron', sans-serif;
  }
.main-disp {
  font-size: 3em;
  padding: 0 1em 0.2em 0;
  min-height: 1em;
}
.mem-disp{
  font-size: 1.5em;
  padding: 1em 2em 0 0;
  border-radius: 1.25em 1.25em 0 0;
}
.main-op{
  display: flex;
  flex-direction: row;
}

#equals{
  flex-grow: 1;
}
#add {
  flex-grow: 1;
}

.btnBox{
  display: flex;
  flex-direction: row;
  flex-wrap: no-wrap;
  max-height: 80%;
}
.numeric{
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-end;
  flex-grow: 1;
  max-height: 80%;
}
.operators{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: justify;
  max-height:80%;
}

@media(min-width:40em){

  #calcbox{
min-width: 35em;
}


}
